#coverflow.container {
  position: relative;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
}
#coverflow .coverflow {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
}
#coverflow .stage {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 500px;
          perspective: 500px;
}
#coverflow .figure {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  cursor: pointer;
  -webkit-transition: -webkit-transform 600ms ease;
  transition: -webkit-transform 600ms ease;
  -o-transition: transform 600ms ease;
  transition: transform 600ms ease;
  transition: transform 600ms ease, -webkit-transform 600ms ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  z-index: 9;
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-box-shadow: 0 50px 70px rgba(0, 0, 0, 0.5);
          box-shadow: 0 50px 70px rgba(0, 0, 0, 0.5);
}
#coverflow .cover {
  display: block;
  width: 100%;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
#coverflow .preloader {
  display: hidden;
}
#coverflow .text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.9em;
  color: white;
  padding: 5px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.6);
}
#coverflow .actions {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
#coverflow .actions .button {
  border: 1px solid #616161;
  border-radius: 2px;
  min-width: 120px;
  padding: 5px;
  margin: 2px;
  background: #616161;
  color: rgba(255, 255, 255, 0.9);
}
#coverflow .actions .button:hover {
  background: black;
  color: white;
}
#coverflow .actions .button:focus {
  outline: none;
}
@-webkit-keyframes prevent {
  0% {
    pointer-events: none;
  }
  100% {
    pointer-events: auto;
  }
}
@keyframes prevent {
  0% {
    pointer-events: none;
  }
  100% {
    pointer-events: auto;
  }
}


